.wrapper {
    overflow-x: hidden;
}

.articlesWrapper {
    display: grid;
    grid-template-columns: 300px 1fr;
    grid-gap: 1em;
}

.navBar {
    /*text-align: center;*/
    padding-left: 20px;
    min-height: 85vh;
    border-right: 1px solid #EBEBE9;
}

.newPopular {
    margin: 66px 0;
    padding: 0 15px;
    padding-right: 25px;
}

.newPopular>div {
    margin: 25px 0;
    font-weight: 500;
    font-size: 21px;
    line-height: 25px;
    cursor: pointer;
    padding-bottom: 5px;
    color: rgba(33, 51, 54, 0.8);
}

.newPopular>div:hover {
    border-bottom: 1px solid black;
}

.filterWrapper {
    margin-right: 20px;
}

.filterWrapper>div {
    margin: 30px 0;
}

.article {
    background: rgba(112, 124, 151, 0.15);
    border-radius: 0 0 6px 6px;
    padding: 30px 25px;
    margin: 20px 40px 40px 20px;
}

.header {
    display: flex;
    align-items: baseline;
}

.category {
    font-style: normal;
    font-weight: 500;
    font-size: 22px;
    line-height: 26px;
    margin-right: 20px;
    color: #64A928;
}

.name {
    font-weight: 500;
    font-size: 26px;
    line-height: 32px;
    margin-right: 20px;
    color: #213336;
}

.title {
    font-weight: 500;
    font-size: 25px;
    line-height: 35px;
    color: rgba(33, 51, 54, 0.8);
    margin-top: 15px;
}

.text>div {
    font-size: 22px;
    line-height: 35px;
    margin-top: 35px;
    color: rgba(33, 51, 54, 0.8);
    display: -webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
}

.ArticleText {
    font-size: 22px;
    line-height: 35px;
    margin-top: 35px;
    color: rgba(33, 51, 54, 0.8);
    display: -webkit-box;
    display: block;
}
.ArticleText>div{
    width: 80%;
}
.ArticleText>div>img{
    /*width: 100%;*/
    max-height: 510px !important;
    margin-top: 20px;
}
.date {
    color: #000000;
}

.likes {
    margin-top: 100px;
    font-size: 22px;
    display: flex;
    align-items: center;
    cursor: pointer;
}

.search {
    position: relative;
}

.submit {
    width: 20px;
    height: 20px;
    border: none;
    background: url('../../img/loop.png') no-repeat 50% 50%;
    position: absolute;
    top: 10px;
    right: 200px;
}

.submit2 {
    background: none;
    height: 20px;
    border: none;
    position: absolute;
    top: 10px;
    right: 20px;
}

.submit2:focus {
    outline: none;
}

.filterCategory {
    font-weight: 500;
    font-size: 26px;
    line-height: 27px;
    margin: 30px 0 14px 10px;
    color: rgba(33, 51, 54, 0.8);
}

.imgWrapper {
    height: 50px;
    display: flex;
    align-items: center;
    padding: 10px;
    border: 1px solid #4F5D5E;
    box-sizing: border-box;
    border-radius: 10px 0 0 10px;
}

.imgWrapper>img {
    margin-right: 10px;
}

.count {
    height: 50px;
    /*font-size: 23px;*/
    display: flex;
    align-items: center;
    padding: 10px;
    border: 1px solid #4F5D5E;
    border-left: none;
    box-sizing: border-box;
    border-radius: 0 10px 10px 0;
}

.blueBorder1 {
    border: 2px solid #0a66b7 !important;
    color: #0a66b7;
}

.blueBorder2 {
    border: 2px solid #0a66b7 !important;
    border-left: none !important;
    color: #0a66b7;
}